<template>
  <div class="legendAnalysis">
    <div class="left">
      <div class="tit">近一年智能ivr流失用户业务办理情况top10</div>
      <div class="chartCon">
        <transverse-bar ref="transverseBar" @changeBarDetail="changeBarDetail"></transverse-bar>
      </div>
      <!--            <line-charts ref="lineCharts" ></line-charts>-->
    </div>
    <div class="right">
      <div class="tit">近一年智能ivr转人工用户业务办理情况</div>
      <!--  @clickBar="getSeatNumebrDetailData"
      @setOption="setOption"-->
      <!--            <line-charts ref="lineCharts2" ></line-charts>-->
      <div class="chartCon2">
        <base-bar-charts
          @changeBarDetail="changeBarDetailCharts"
          :legendData="tdzscLegendData"
          :showClick="true"
          :chartsData="tdzscArray"
          :xNameData="tdzscXdata"
          v-if="show4"
        ></base-bar-charts>
      </div>
      <div class="tit">{{month}}月智能ivr转人工用户业务办理情况top5</div>
      <div class="chartCon2">
        <base-pie-charts :legendData="legendData" :chartsData="ydlbArray" v-if="show1"></base-pie-charts>
      </div>
    </div>
  </div>
</template>

<script>
// import lineCharts from '../incomingClient/template/lineCharts'
import baseBarCharts from "@/components/charts/base-bar-charts";
import basePieCharts from "@/components/charts/base-pie-charts";
import transverseBar from "./components/transverse-bar";
import Interface from "@/base/mixin/user-behavior";
export default {
  name: "legendAnalysis",
  mixins: [Interface],
  components: {
    // lineCharts,
    transverseBar,
    baseBarCharts,
    basePieCharts
  },
  props: {},
  data() {
    return {
      ydlbArray: [],
      show1: false,
      legendData: [],
      tdzscArray: [],
      tdzscXdata: [],
      show4: false,
      month: "",
      tdzscLegendData: []
    };
  },
  mounted() {
    this.initData();
    /***
       * 上一个版本代码2021-11-2
      this.GetIvrStable()
      this.GetIvrTrend()
       **/
  },
  methods: {
    changeBarDetail(name) {
      window.console.log("changeBarDetail", name);
      this.show4 = false;
      this.businessSituation({ businessType: name }).then(res => {
        this.tdzscArray = res.map(item => {
          return item.sl;
        });
        this.tdzscXdata = res.map(item => {
          return item.yf;
        });
        this.tdzscLegendData = res.map(item => {
          return item.ywlb;
        });
        this.show4 = true;
        this.changeBarDetailCharts(res[0].yf);
      });
    },
    changeBarDetailCharts(name) {
      window.console.log("***", name);
      this.show1 = false;
      this.businessTypeSpread({ month: name }).then(res => {
        res.forEach(item => {
          item.name = item.ywlb;
          item.value = item.sl;
        });
        this.ydlbArray = res;
        this.legendData = res.map(item => {
          return item.ywlb;
        });
        this.month = name;
        this.show1 = true;
      });
    },
    initData() {
      this.topTenTheYear({}).then(res => {
        this.$refs.transverseBar.init(
          res.map(item => {
            return item.ywlb;
          }),
          res.map(item => {
            return item.sl;
          })
        );
        this.changeBarDetail(res[0].ywlb);
      });
    }

    /***
       * 下面代码为上一个版本2021-11-2
       *
       *
      //近一年智能IVR用户稳定性分布
      GetIvrStable(){
        this.getIvrStable({}).then(res=>{
          let legendData = ["近一年智能IVR用户稳定性分布"];
          let dataX = [];
          let series = [{
            name: '近一年智能IVR用户稳定性分布',
            type: 'line',
            // stack: '总量',
            color:'#00a6ff',
            smooth: true,
            data: []
          }] //[{name: '邮件营销',type: 'line',stack: '总量',smooth: true,data: [120, 132, 101, 134, 90, 230, 210]}]
          res.forEach(item=>{
            dataX.push(item.YF)
            series[0].data.push(item.SL)
          })
          this.$nextTick(()=>{
            this.$refs.lineCharts.init(legendData, series, dataX)
          })
        })
      },
      GetIvrTrend(){
        this.getIvrTrend({}).then(res=>{
          let legendData2 = ["每月智能IVR应用趋势分布"];
          let dataX2 = [];
          let series2 = [{
            name: '每月智能IVR应用趋势分布',
            type: 'line',
            // stack: '总量',
            color:'#ff8000',
            smooth: true,
            data: []
          }]
          res.forEach(item=>{
            dataX2.push(item.YWLB)
            series2[0].data.push(item.SL)
          })
          this.$nextTick(()=>{
            this.$refs.lineCharts2.init(legendData2, series2, dataX2)
          })
        })

      },
       **/
  }
};
</script>

<style scoped lang="scss">
.legendAnalysis {
  display: flex;
  height: 100%;
  .tit {
    height: 30px;
    line-height: 30px;
    padding-left: 12px;
    /*border-left: 8px solid #1A84F4;*/
    font-weight: bold;
    color: #1a84f4;
  }
  .chartCon {
    height: calc(100% - 30px);
  }
  .left {
    height: 100%;
    flex: 2;
    margin-right: 20px;
    background-color: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .right {
    flex: 3;
    background-color: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
    height: 100%;
    .chartCon2 {
      box-sizing: border-box;
      height: calc(50% - 30px);
      background-color: #ffffff;
      border-top: 2px solid rgba(26, 132, 244, 0.1);
      border-bottom: 2px solid rgba(26, 132, 244, 0.1);
    }
  }
}
</style>